* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app_wrapper {
    display: grid;
    grid-template-areas:
     'header header'
     'navbar content';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
    width: 1200px;
}

.app_header {

    grid-area: header;
    background-color: darkslategray;
    width: 100%;
}

.navbar {
    position: fixed;
    grid-area: navbar;
    height: 100%;

    background-color: darkslategray;

}

.app_content {
    grid-area: content;
    background-color: lavenderblush;

}

.app_header_logo {
    width: 50px;
    height: 50px;
}

.navbar_list {
    list-style-type: none;
}

.navbar_link {
    text-decoration: none;
    color: whitesmoke;
}


